.main-container {
  position: relative;
  overflow: hidden;

  &::after {
    position: absolute;
    content: "";
    width: 602px;
    height: 1364px;
    top: 782px;
    right: 0;
    background-image: url("../img/clouds-desktop.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    z-index: -1;
  }
}
@include vp-1199 {
  .main-container::after {
    width: 301px;
    height: 1364px;
    top: 929px;
    background-image: url("../img/clouds-tablet.png");
  }
}

@include vp-767 {
  .main-container::after {
    width: 176px;
    height: 595px;
    top: 705px;
    background-image: url("../img/clouds-mobile.png");
  }
}
