.contacts {
  padding: 60px 120px 104px;
  display: grid;
  grid-template-columns: 1fr 1fr;

  h2 {
    @include h2-desktop;
    margin-bottom: 26px;
    max-width: 483px;
    grid-column: 1 / 3;
  }

  p {
    @include text-regular-desktop;
    margin-bottom: 50px;
    max-width: 483px;
    grid-column: 1 / 2;
  }

}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 35px;
  max-width: 483px;
  grid-column: 1 / 2;
}

.contacts__item {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  gap: 8px;
  padding-left: 56px;

  &::after {
    position: absolute;
    content: "";
    width: 26px;
    height: 26px;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
  }

  &--tel::after {
    background-image: url("../img/stack.svg#phone");
  }

  &--e-mail::after {
    background-image: url("../img/stack.svg#mail");
  }

  &--address::after {
    background-image: url("../img/stack.svg#pin-stroke");
  }

  &--tel,
  &--e-mail {
    @include link-style;
  }

  dt {
    font-family: "Montserrat SemiBold", "Arial", sans-serif;
    font-size: 18px;
    line-height: 100%;
    margin: 0;
    color: $color-elephant;
  }

  dd {
    margin: 0;
    @include text-regular-desktop;
  }
}

.contacts__map {
  width: 585px;
  height: 324px;
  background-image: url(../img/map.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  grid-column: 2 / 3;
  grid-row: 2 / 4;
  margin: auto 0 0 auto;
}

@include vp-1199 {
  .contacts {
    padding: 70px 45px;
    display: block
  }

  .contacts h2 {
    @include h2-tablet;
    margin-bottom: 20px;
  }

  .contacts p {
    max-width: 483px;
  }

  .contacts__list {
    flex-direction: row;
    column-gap: 36px;
    max-width: none;
    width: 100%;
    margin-bottom: 40px;
  }

  .contacts__item {
    padding-left: 34px;
    gap: 6px;
  }

  .contacts__item::after {
    width: 24px;
    height: 24px;
  }

  .contacts__item dt {
    font-size: 16px;
  }

  .contacts__item dd {
    @include text-small-tablet;
  }

  .contacts__map {
    margin: 0 auto;
  }

  .contacts__map iframe {
    display: block;
    width: 100%;
    height: 340px;
  }
}

@include vp-767 {}
